export default {
	onLoad (options) {
		console.log(plus.push.getClientInfo())
		this.cid = plus.push.getClientInfo().clientid
		console.log(options)
		if( options.wxlogin) {
			console.log('进入')
			this.wxlogin = options.wxlogin
			this.uniId = options.unionId
			this.nickName = options.nickName
			this.avatarUrl = options.avatarUrl
		}
		
	},
	data () {
		return {
			//是否是微信进来
			wxlogin: '',
			uniId: '',
			
			isReader: true,
			//验证码，倒计时
			getYzm: true,
			computedTimer: 0,
			//手机号
			userPhone: '',
			//验证码
			code: '',
			yzm: '',
			//密码
			userPwd: '',
			//重复密码
			userSecPwd: '',
			//邀请码
			inviteCode: '',
			resMsg: '',
			cid: '',
			checkPhone: '',
			nickName: '',
			avatarUrl: ''
		}
	},
	methods: {
		checkPhoneNumber() {
			let reg = /^1[3456789]\d{9}$/;
			if (reg.test(this.userPhone)) {
				this.checkPhone = false
				return true
			} else {
				this.checkPhone = true
				return false
			}
		},
		//改阅读
		handleIsReader() {
			this.isReader = !this.isReader
		},
		//验证码
		async getCode () {
			this.checkPhoneNumber()
			
			if (!this.getYzm) return
			
			if(this.userPhone.trim() == '' ) {
				uni.showToast({
					icon: 'none',
					title: '请先输入手机号'
				})
				return
			} 
			
			if (this.checkPhone) {
				uni.showToast({
					icon: 'none',
					title: '请检查手机号输入是否正确'
				})
				return
			}
			
			this.getYzm = false 
			this.computedTimer = 60
			const timer = setInterval(() => {
				if (this.computedTimer === 0) {
					clearInterval(timer)
					this.getYzm = true
				} else {
					this.computedTimer--
				}
			},1000)
			let res = await this.$fetch(this.$api.customerPhone,{mobile:this.userPhone},'GET','form')
			
			this.yzm = res.msg
		},
		//提交
		async formSubmit () {
			if (!this.wxlogin) {
				
			
			if (this.userPhone.trim() == '' || this.code.trim() == '' || this.userPwd == '' || this.userSecPwd == '') {
				uni.showToast({
					icon:'none',
					title:'请填写信息后进行注册'
				})
				return
			}
			if (this.userPwd != this.userSecPwd) {
				uni.showToast({
					icon:'none',
					title: '两次输入的密码不一致'
				})
				return
			}
			// if (this.yzm !== this.code) {
			// 	uni.showToast({
			// 		icon:'none',
			// 		title: '验证码输入错误'
			// 	})
			// 	return
			// }
			if (!this.isReader){
				uni.showToast({
					icon:'none',
					title: '请认真阅读平台用户协议'
				})
				return
			}
			let res = await this.$fetch(this.$api.customerUser,{mobile: this.userPhone,checkCode: this.code,password: this.userPwd,inviteCode: this.inviteCode,cid:this.cid},'POST','form')
			if (res.code === 0) {
				uni.showToast({
					icon: 'success',
					title: '注册成功'
				})
				
				let msg = await this.$fetch(this.$api.customerLogin,{loginName: this.userPhone,password: this.userPwd,cid:this.cid},'POST',"Form")
				if (msg.code == 0) {
					uni.setStorageSync('token',msg.data.token)
					// uni.showToast({
					// 	icon:'success',
					// 	title: '登录成功'
					// })
					setTimeout(() => {
						uni.switchTab({
							url:'../my/my'
						})
					},1500)
				} else {
					uni.showToast({
						icon:'none',
						title: msg.msg
					})
				}
				
				// setTimeout(() => {
				// 	uni.reLaunch({
				// 		url: '../enter/enter'
				// 	})
				// },1500)
			} else {
				uni.showToast({
					icon: 'none',
					title: res.msg
				})
			}
			} else {
				//微信注册
				console.log({avatarUrl:this.avatarUrl,nickName:this.nickName,mobile: this.userPhone,checkCode: this.code,inviteCode: this.inviteCode,cid:this.cid,unionId:this.uniId})
				let res = await this.$fetch(this.$api.customerUser,{avatarUrl:this.avatarUrl,nickName:this.nickName,mobile: this.userPhone,checkCode: this.code,inviteCode: this.inviteCode,cid:this.cid,unionId:this.uniId},'POST','form')
				
				if (res.code === 0) {
					
					
					
					let msg = await this.$fetch(this.$api.customerWx,{cid:this.cid,unionId:this.uniId},'POST','FORM')
					
					
					if (msg.code === 0) {
						uni.setStorageSync('token',msg.data.token)
						uni.switchTab({
							url: '../my/my'
						})
					} else {
						uni.showToast({
							icon:'none',
							title:res.msg
						})
					}
					
				} else {
					uni.showToast({
						icon:'none',
						title:res.msg
					})
				}
			}
		},
		//协议
		async goToRichText () {
			
			uni.navigateTo({
				url: '../rich-text/rich-text?id='+ 2
			})
		}
	}
}
